@import "variables";

//----------MAIN----------
.fa {
  text-align: center;
}


.form-inline {
  .dropdown {
    display: inline-block;
  }
}

.nav-sidebar {
  li {
    position: relative;
    list-style: outside none none;
    display: block;
  }

  ul {

    a {
      text-indent: 2em;
    }

  }
  .active {
    a {
      background-color: #428bca;
      color: #fff;
    }
    a:hover {
      background-color: #428bca;
      color: #fff;
    }
    a:focus {
      background-color: #428bca;
      color: #fff;
    }
  }
}

.form-group {
  .inline {
    display: inline;
  }

  #output {
    button {
      right: 0%;
      top: 0%;
    }
  }
}